<template>
  <div>
    <div v-if="true">
      <div style="padding: 15px">

        <b-table
            id="technologyTable"
            :items="records"
            :fields="column"
            striped
            hover
            :per-page="perPage"
            :current-page="currentPage"
            @row-clicked="handleRowClicked"
            fixed
            :sort-by.sync="sortBy"
        >

          <template #head(toggle)="data">
            <b-icon icon="info-square-fill" font-scale="1.2"></b-icon>
          </template>

          <template #cell(toggle)="data">
            <b-icon icon="plus" font-scale="1.5" :id="'plus'+data.item.name"></b-icon>
          </template>
          <template slot="row-details">
            <div v-if="h != undefined">
              <b-row cols="1" cols-lg="2">
                <b-col class="align-left">

                  <b-list-group class="text-left">
                    <b-list-group-item><b>Description: </b>{{h.information.qualitative.description}}</b-list-group-item>
                    <b-list-group-item>
                      <!-- number of people served per unit -->
                      <span>
                        <icon-base :id="'peoplePerUnit'+h.name" class="solid-border" :class="selectGreen(h.information.quantitative.people_served.charAt(0))" width="50" height="50" icon-color="#011E41"><icon-people-per-unit /></icon-base>
                    </span>
                      <b-tooltip :target="'peoplePerUnit'+h.name" triggers="hover">
                        <b>Water throughput: </b>
                        <span v-if="h.information.quantitative.people_served.charAt(0) == 'L'">{{ 'Low' + h.information.quantitative.people_served.substr(1) }}</span>
                        <span v-else-if="h.information.quantitative.people_served.charAt(0) == 'M'">{{ 'Medium' + h.information.quantitative.people_served.substr(1) }}</span>
                        <span v-else-if="h.information.quantitative.people_served.charAt(0) == 'H'">{{ 'High' + h.information.quantitative.people_served.substr(1) }}</span>
                        <span v-else>Unknown</span>

                      </b-tooltip>

                      <!-- Water throughput -->
                      <span><icon-base :id="'literPerHour'+h.name" width="50" height="50" class="solid-border" :class="selectGreen(h.information.quantitative.water_throughput.charAt(0))"><icon-water-litre-per-hour /></icon-base></span>
                      <b-tooltip :target="'literPerHour'+h.name" triggers="hover">
                        <b>Water throughput: </b>
                        <span v-if="h.information.quantitative.water_throughput.charAt(0) == 'L'">{{ 'Low' + h.information.quantitative.water_throughput.substr(1) }}</span>
                        <span v-else-if="h.information.quantitative.water_throughput.charAt(0) == 'M'">{{ 'Medium' + h.information.quantitative.water_throughput.substr(1) }}</span>
                        <span v-else-if="h.information.quantitative.water_throughput.charAt(0) == 'H'">{{ 'High' + h.information.quantitative.water_throughput.substr(1) }}</span>
                        <span v-else>Unknown</span>

                      </b-tooltip>

                      <!-- lifespan -->
                      <span><icon-base :id="'lifespan'+h.name" width="50" height="50" class="solid-border"><icon-lifespan/></icon-base></span>
                      <b-tooltip :target="'lifespan'+h.name" triggers="hover">
                        <b>Lifespan:</b> {{h.information.quantitative.lifetime_L}}<span v-if="h.information.quantitative.lifetime_L && h.information.quantitative.lifetime_Y">,</span>  {{h.information.quantitative.lifetime_Y}}
                      </b-tooltip>

                      <!-- Safe water storage capacity-->
                      <span><icon-base :id="'safestorage'+h.name" width="50" height="50" class="solid-border"><icon-safe-storage/></icon-base></span>
                      <b-tooltip :target="'safestorage'+h.name" triggers="hover">
                        <b>Safe water storage capacity:</b> {{h.information.quantitative.water_storage_capacity}}
                      </b-tooltip>

                      <!-- Standalone technology -->
                      <span><icon-base :id="'standalone'+h.name" width="50" height="50" class="solid-border"><icon-standalone/></icon-base></span>
                      <b-tooltip :target="'standalone'+h.name" triggers="hover">
                        <b>Standalone technology:</b> {{h.information.quantitative.standalone_tech}}
                      </b-tooltip>

                      <!-- Water source -->
                      <span><icon-base :id="'water_source'+h.name" width="50" height="50" class="solid-border"><icon-water-source/></icon-base></span>
                      <b-tooltip :target="'water_source'+h.name" triggers="hover">
                        <b>Possible water sources:</b> {{h.information.quantitative.water_source}}
                      </b-tooltip>

                      <!-- Availability in local market -->
                      <span><icon-base :id="'available'+h.name" width="50" height="50" class="solid-border"><icon-available/></icon-base></span>
                      <b-tooltip :target="'available'+h.name" triggers="hover">
                        <b>Technology available on the local market:</b> {{h.information.quantitative.availability_local_market}}
                      </b-tooltip>

                      <!-- apex -->
                      <span><icon-base :id="'apex'+h.name" width="50" height="50" class="solid-border"><icon-apex/></icon-base></span>
                      <b-tooltip :target="'apex'+h.name" triggers="hover">
                        <b>CAPEX:</b> {{h.information.quantitative.capex_no_shipping}}
                        <br>
                        <b>Price per m3:</b> {{h.information.quantitative.capex_usd_m3}}
                      </b-tooltip>

                      <!-- Size and weight -->
                      <span><icon-base :id="'size'+h.name" width="50" height="50" class="solid-border"><icon-size-weight/></icon-base></span>
                      <b-tooltip :target="'size'+h.name" triggers="hover">
                        <b>Size and weight:</b> {{h.information.quantitative.size_weight}}
                      </b-tooltip>

                      <!-- Manufacturer -->
                      <span><icon-base :id="'manufacturer'+h.name" width="50" height="50" class="solid-border"><icon-manufacturer/></icon-base></span>
                      <b-tooltip :target="'manufacturer'+h.name" triggers="hover">
                        <b>Manufacturer:</b> {{h.information.qualitative.manufacturer}}
                      </b-tooltip>
                    </b-list-group-item>
                    <b-list-group-item><b>Previously used:</b> <span v-html='URLify(h.information.quantitative.previously_used)'></span></b-list-group-item>
                    <b-list-group-item :href="h.information.qualitative.link" target='_blank'>
                      <b-row class="m-auto">
                        <b-col cols="auto" class="m-auto">
                          <icon-base icon-name="link" width="50" height="50" ><icon-link /></icon-base>
                        </b-col>
                        <b-col class="m-auto">
                          About {{h.name}}
                        </b-col>
                      </b-row>
                    </b-list-group-item>
                  </b-list-group>
                </b-col>
                <b-col>
                  <HorizontalBarChart :chart-data="fillData([h])" :options="chartOptions"/>
                  <br>

                </b-col>
              </b-row>
            </div>
          </template>
        </b-table>
        <b-pagination
            class = "toTheRight"
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="technologyTable"
            size="sm"
            @page-click="resetTable"

        ></b-pagination>
      </div>
    </div>
    <div v-else>Select some criteria first</div>
  </div>

</template>

<script>

import IconBase from './IconBase.vue';
import IconLink from './icons/IconLink.vue';
import IconPeoplePerUnit from './icons/IconPeoplePerUnit';
import IconWaterLitrePerHour from "@/components/icons/IconWaterLitrePerHour";
import IconLifespan from "@/components/icons/IconLifespan";
import IconSafeStorage from "@/components/icons/IconSafeStorage";
import IconStandalone from "@/components/icons/IconStandalone";
import IconWaterSource from "@/components/icons/IconWaterSource";
import HorizontalBarChart from "@/components/HorizontalBarChart";
import IconAvailable from "@/components/icons/IconAvailable";
import IconApex from "@/components/icons/IconApex";
import IconSizeWeight from "@/components/icons/IconSizeWeight";
import IconManufacturer from "@/components/icons/IconManufacturer";

export default {
  name: "technologies",
  components: {
    IconManufacturer,
    IconSizeWeight,
    IconApex,
    IconAvailable,
    IconWaterSource,
    IconStandalone,
    IconSafeStorage,
    IconLifespan,
    IconWaterLitrePerHour,
    HorizontalBarChart,
    IconBase,
    IconLink,
    IconPeoplePerUnit,

  },
  data() {
    return{
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        scales: {
          xAxes: [{
            ticks: {
              stepSize: 20,
              beginAtZero: true,
            }
          }],
          yAxes: [{
            ticks: {
              callback: function(s) {
                if(s == "Easiness of use") return ["Easiness", "of use"]
                else if (s == "Instantaneous water supply") return ["Instantaneous", "water supply"]
                else if (s == "Maintenance") return "Maintenance"
                else if (s == "Power requirements") return ["Power", "requirements"]
                else if (s == "Taste alteration") return ["Taste", "alteration"]
                else if (s == "Waste generation") return ["Waste", "generation"]
                else if (s == "WHO Performance target") return ["WHO", "Performance", "target"]
              },
            }
          }],
        },
        tooltips: {
          displayColors: false,
          enabled: true,
          mode: 'label',
          callbacks: {
            label: function(tooltipItems, data) {
              let idx = tooltipItems.index;
              return "Score:"+data.datasets[0].data[idx];

            }
          }
        },
      },

     //Proves per taula
      sortBy: '',
      allOpenRows: [],
      h: undefined,
      selectAll: false,
      perPage: 6,
      currentPage: 1,
      column: [
        {
          key: "name",
          sortable: true,
          label: "Name",
        },
        {
          key: "score",
          sortable: true,
          label: "Score",
        },
        {
          key: "toggle",
          label: "",
        },
      ],
      records: this.technologies
    };
  },
  props: {
    technologies: Array,
    activeIntrinsic: Array,
    activeContext: Array,
  },
  watch:{
    technologies: function(newVal,oldVal) {
      console.log("watch")
      this.records = newVal
      this.resetTable()
      this.currentPage = 1
      this.sortBy= ''
    }
  },

  activated: function() {
    console.log("activat")
    this.records = this.technologies
    this.resetTable()
    this.currentPage = 1
    this.sortBy= ''
  },

  computed: {
    rows() {
      return this.records.length
    }
  },
  methods: {
    resetTable: function(){
      console.log("reset")
      this.records.map((elem) =>{
        this.$set(elem, "_showDetails", false);
        let elemToClose = document.getElementById('plus'+elem.name);
        if(elemToClose != null) elemToClose.classList.remove("rotated");
      })

      this.allOpenRows = []
      this.h = undefined
    },

    handleRowClicked(item, index) {
      this.allOpenRows.map((ele) => {
        if (ele.name !== item.name && ele._showDetails) {
          this.$set(ele, "_showDetails", !ele._showDetails);
        }
      });

      let previouslyOpened = undefined
      if (this.allOpenRows.length > 0){
        previouslyOpened = document.getElementById('plus'+this.allOpenRows[0].name);
      }
      let clickedElem = document.getElementById('plus'+item.name);

      this.allOpenRows = [];
      this.h = undefined
      this.$set(item, "_showDetails", !item._showDetails);

      if(!previouslyOpened || (previouslyOpened.id != clickedElem.id)){
        this.allOpenRows.push(item);
        this.h = item
      }

      if(previouslyOpened){
        previouslyOpened.classList.remove("rotated");
      }

      if (!previouslyOpened || (previouslyOpened && (previouslyOpened.id != clickedElem.id))){
        clickedElem.classList.add("rotated");
      }

    },
    random_rgba: function() {

      let red = 'rgb(239, 51, 64)'
      let blue = 'rgb(1, 30, 65)'
      let orange = '#ff5116'
      let blue2 = '#009ddb'
      let green1 = '#545f1d'
      let green2 = '#8fd400'
      let pink1 = '#5c3160'
      let pink2 = '#d40072'

      let colors =[red, blue, orange, blue2, green1, green2, pink1, pink2]

      let randomIndex = Math.floor(Math.random() * colors.length);
      return colors[randomIndex]

      },
    prova: function (){
     let aux = HorizontalBarChart
      aux.update()
    },
    URLify: function (string){
      const urls = string.match(/(([a-z]+:\/\/)?(([a-z0-9\-]+\.)+([a-z]{2}|aero|arpa|biz|com|coop|edu|gov|info|int|jobs|mil|museum|name|nato|net|org|pro|travel|local|internal))(:[0-9]{1,5})?(\/[a-z0-9_\-\.~]+)*(\/([a-z0-9_\-\.]*)(\?[a-z0-9+_\-\.%=&amp;]*)?)?(#[a-zA-Z0-9!$&'()*+.=-_~:@/?]*)?)(\s+|$)/gi);
      if (urls) {
        urls.forEach(function (url) {
          string = string.replace(url, '<a target="_blank" href="' + url + '">' + "Further info here " + "</a>");
        });
      }
      return string.replace("(", "<br/>(");
    },

    getLabels: function(keys){
      let labels = keys.map(s => {
        return this.keyToLabel(s)
      })
      return labels
    },
    keyToLabel: function (s){
      if(s == "easiness_use") return "Easiness of use"
      else if (s == "inst_water_supply") return "Instantaneous water supply"
      else if (s == "maintenance") return "Maintenance"
      else if (s == "power_requirements") return "Power requirements"
      else if (s == "taste_alteration") return "Taste alteration"
      else if (s == "waste_generation") return "Waste generation"
      else if (s == "who_perf_target") return "WHO Performance target"

    },
    getData: function(t){
      return {
        data: this.activeIntrinsic.map(s => {
          return t.criteria.intrinsic[s]
        }),
        //backgroundColor: '#'+(0x1000000+Math.random()*0xffffff).toString(16).substr(1,6), //Random color
        backgroundColor: this.random_rgba(),
        label: t.name
      }
    },
    fillData: function(technologies){
      let chartData = {
        labels: this.getLabels(this.activeIntrinsic),
        datasets: technologies.map(t => {
          return this.getData(t)
        }),
      }

      return chartData
    },

    selectGreen: function(s){
      if(s == "L") return 'low-green'
      else if(s == "M") return 'medium-green'
      else if(s == "H") return 'high-green'
    },

  }
}
</script>

<style scoped>
  /* https://markheath.net/post/font-awesome-circle-background */
  .solid-border {
    border-radius: 100%;
    text-align: center;
    line-height: 100px;
    border-style: solid;
    border-color: black;
    border-width: 2px;
  }

  .high-green {
    background: #00AD37;
  }
  .medium-green {
    background: #71CF00;
  }
  .low-green {
    background: #D3E100;
  }

  span {
    padding-right: 3px;
  }
  .small-btn {
    width: 50px;
    height: 25px;
  }
  .toTheRight {
    float: right;
  }
  .rotated {
    transform: rotate(45deg); /* Equal to rotateZ(45deg) */
  }
  .notransition {
    transform: none;
  }

</style>
