<script>
import { HorizontalBar, mixins} from "vue-chartjs";

export default {
  props: {
    chartData: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    },
  },
  mixins: [mixins.reactiveProp],
  extends: HorizontalBar,
  mounted() {
    this.generateLegend()
    this.renderChart(
        this.chartData,
        this.options
    );
  },
};
</script>
