
/** CONTEXT CRITERIA **/
function Method(technology) {
  this.name = "method";
  this.type = "context";
  this.score = function (weight, userOption) {
    let value = technology['criteria']['context']['method'];
    let s = (value === userOption) ? 100 * weight : 0;
    return s;
  }
}

