function TurbidWater(technology) {
  this.name = "treats_turbid_water";
  this.type = "context";
  this.score = function (weight, userOption) {
    let value =  technology['criteria']['context']['treats_turbid_water'];
    let s = ((value === userOption) || (value === 'Yes')) ? 100 * weight : 0;
    return s;
  }
}

