<template>

  <div class="full-width">
    <div class="pb-5">
      <b-navbar class='navbar navbar-default' toggleable="md" type="dark" fixed="top" >
        <b-navbar-brand class="title" >
          Spanish Red Cross
          <br>
          HWTS DSS
        </b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"/>
        <b-collapse id="nav-collapse" is-nav rigth>
          <b-navbar-nav class="ml-auto title">
            <b-nav-item href="#" v-on:click="tab = 1" :class="{active : tab === 1}">RANKED TECHNOLOGIES</b-nav-item>
            <b-nav-item href="#" v-on:click="tab = 2" :class="{active : tab === 2}">AVAILABLE TECHNOLOGIES</b-nav-item>
            <b-nav-item href="#" v-on:click="tab = 3" :class="{active : tab === 3}">CRITERIA</b-nav-item>
            <b-nav-item href="#" v-on:click="tab = 4" :class="{active : tab === 4}">HOME</b-nav-item>
            <!--<b-modal id="modal-info" centered ok-only title="INFO">
              <p class="my-4">Hello from modal!</p>
            </b-modal> -->
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>

    <div class="p-4 main" id="content">
      <div v-if="this.tab === 1">
        <div class="text-justify">
          <hr class="solid">
          <p>Technologies are ranked and scored based on the criteria you have selected.
            Select a technology to see more information.</p>
        </div>
        <keep-alive>
          <Technologies key="top6" :technologies="this.best_technologies" :active-intrinsic="active_intrinsic"/>
        </keep-alive></div>
      <div v-else-if="this.tab === 2">
        <div class="text-justify">
          <hr class="solid">
          <p>Select a technology to see more information. These scores are only based on the <b>intrinsic
            criteria.</b></p>
        </div>
        <keep-alive>
          <Technologies key="available" :technologies="this.technologies" :active-intrinsic="active_intrinsic"/>
        </keep-alive></div>
      <div v-else-if="this.tab === 3">
        <keep-alive>
          <Criteria @childCriteria="chosenCriteria"/>
        </keep-alive></div>
      <div v-else-if="this.tab === 4">
        <keep-alive>
        <div class="Home">
          <hr class="solid">
          <p class="text-justify">Welcome to the household water treatment (HWT) decision support system (DSS) of the Water and
            Sanitation Emergency Response Unit from the Spanish Red Cross. The goal of this tool is to
            provide decision support when choosing technologies to treat drinking water at household
            level in emergency situations.</p>
          <br>
          <About></About>
          <div class="mt-3">
            <b-button pill size="sm" class="m-2 title" style="width: 11rem; background-color: var(--blue-color)" v-on:click="tab = 3"><b>Start using the tool</b></b-button>
          </div>
        </div>
        </keep-alive></div>
    </div>


  </div>

</template>


<script>
import XLSX from 'xlsx';
import Technologies from "@/components/Technologies";
import Criteria from "@/components/Criteria";
import Home from "@/components/Home";
import About from "@/components/About";

import {
  Method,
  HouseholdInstitution,
  TurbidWater,
  WhoPerfTarget,
  Maintenance,
  InstWaterSupply,
  PowerRequirement,
  EasinessUse,
  TasteAlteration,
  WasteGeneration
} from '@/utils/criteria'

export default {
  name: 'Home',
  components: {
    About,
    Home,
    Technologies,
    Criteria,
  },

  data(){
    return{
      technologies: [],
      criteria_to_compute: [],
      tab: 4,
      active_intrinsic: ["easiness_use", "inst_water_supply", "maintenance", "power_requirements", "taste_alteration", "waste_generation", "who_perf_target"],
      best_technologies: [],
    };
  },
  created: function(){
    var start = new Date().getTime();
    let url = "HWTSDSS/technologies_data.xlsx"  //Files placed in the public directory can be accessed directly
    let _this = this;
    var oReq = new XMLHttpRequest();
    oReq.open("GET", url, true);
    oReq.responseType = "arraybuffer";
    oReq.onload = function(e) {
      var arraybuffer = oReq.response;
      /* convert data to binary string */
      var data = new Uint8Array(arraybuffer);
      var arr = new Array();
      for (var i = 0; i !== data.length; ++i) arr[i] = String.fromCharCode(data[i]);
      var bstr = arr.join("");
      /* Call XLSX */
      var workbook = XLSX.read(bstr, {type: "binary"});
      /* DO SOMETHING WITH workbook HERE */
      var first_sheet_name = workbook.SheetNames[0];
      /* Get worksheet */
      var worksheet = workbook.Sheets[first_sheet_name];
      let excelData = XLSX.utils.sheet_to_json(worksheet, {raw: true})
      //create nested attributes from json read from excel
      _this.technologiesData(excelData);
    };
    oReq.send();

  },
  methods: {
    technologiesData: function(data){
      let id = 1;
      let _this = this;
      data.forEach(function (tech){
        const auxTech = {
          id: id,
          name: tech['technology'],
          criteria: {
            context: {
              residual_chlorine: tech['residual_chlorine'],
              method: tech['method'],
              household_institution: tech['household_institution'],
              treats_turbid_water: tech['treats_turbid_water']
            },
            intrinsic: {
              who_perf_target: tech['who_perf_target'],
              maintenance: tech['maintenance'],
              inst_water_supply: tech['inst_water_supply'],
              power_requirements: tech['power_requirements'],
              easiness_use: tech['easiness_use'],
              taste_alteration: tech['taste_alteration'],
              waste_generation: tech['waste_generation']
            }
          },
          information: {
            qualitative: {
              manufacturer: tech['manufacturer'],
              description: tech['description'],
              link: tech['link']
            },
            quantitative: {
              people_served: tech['people_served'],
              water_throughput: tech['water_throughput'],
              lifetime_L: tech['lifetime_L'],
              lifetime_Y: tech['lifetime_Y'],
              water_storage_capacity: tech['water_storage_capacity'],
              standalone_tech: tech['standalone_tech'],
              water_source: tech['water_source'],
              availability_local_market: tech['availability_local_market'],
              previously_used: tech['previously_used'],
              capex_usd_m3: tech['capex_usd_m3'],
              capex_no_shipping: tech['capex_no_shipping'],
              size_weight: tech['size_weight']
            }
          },
          score: 0
        }
        _this.best_technologies.push(auxTech);  //at first, "best_technologies" have them all 0 scored
        let initialScore = 0.00;
        for (const [key, value] of Object.entries(auxTech.criteria.intrinsic)) {
          initialScore += value;
        }
        initialScore = Math.round(((initialScore / 7) + Number.EPSILON) * 100) / 100;
        const auxTech_2 = JSON.parse(JSON.stringify(auxTech));
        auxTech_2.score =  initialScore;
        _this.technologies.push(auxTech_2);     //"technologies" with its base score (considering only intrinsic criteria)
        id++;
      });
    },
    chosenCriteria: function (params) {
      let _this = this;
      _this.criteria_to_compute = params;

    },
    computeScore: function (techList) {
      let _this = this;
      let residualChlorine = _this.criteria_to_compute.context_values.residual_protection
      let techCopy = [...techList]; //copy of original technologies array
      //let techCopy = _this.technologies;
      techCopy.forEach(function (tech) {
        if((tech.criteria.context.residual_chlorine === residualChlorine) || (residualChlorine === 'No')) { //residual protection input matches with technology
          let method = new Method(tech);
          let hhInst = new HouseholdInstitution(tech);
          let turbidWater = new TurbidWater(tech);
          let who = new WhoPerfTarget(tech);
          let maintenance = new Maintenance(tech);
          let waterSupply = new InstWaterSupply(tech);
          let powerReq = new PowerRequirement(tech);
          let use = new EasinessUse(tech);
          let tasteAlt = new TasteAlteration(tech);
          let wasteGen = new WasteGeneration(tech);

          let context = [];
          let intrinsic = [];
          context.push(method, hhInst, turbidWater);
          intrinsic.push(who, maintenance, waterSupply, powerReq, use, tasteAlt, wasteGen);

          let computedScore = 0;
          intrinsic.forEach(function (c) {
            let idx = _this.criteria_to_compute.chosen_criteria.findIndex(x => x.name === c.name); //idx of the criteria in the list of "criteria_to_compute".
            let idx_intrinsic = _this.active_intrinsic.findIndex(x => x === c.name); //idx of the criteria in the list of "active_intrinsic"
            if (idx !== -1) { //the criteria has to be considered when computing the score.
              let criteriaScore = c.score(_this.criteria_to_compute.chosen_criteria[idx].weight)
              computedScore += criteriaScore;
              if(idx_intrinsic === -1) _this.active_intrinsic.push(c.name) //add criteria to the list of "active intrinsic"
            }else {
              if(idx_intrinsic !== -1) _this.active_intrinsic.splice(idx_intrinsic, 1); //delete criteria from the list of "active intrinsic"
            }
          });
          context.forEach(function (c) {
            let idx = _this.criteria_to_compute.chosen_criteria.findIndex(x => x.name === c.name);
            if (idx !== -1) { //the criteria has to be considered when computing the score.
              let criteriaScore = c.score(_this.criteria_to_compute.chosen_criteria[idx].weight, _this.criteria_to_compute.context_values[c.name])
              computedScore += criteriaScore;
            }
          });
          tech.score = Math.round(((computedScore / 100) + Number.EPSILON) * 100) / 100;
        }
        else //residual protection input does not match with technology
          tech.score = 0;
      });
      techCopy.sort(function (a,b) {
        const scoreA = a.score;
        const scoreB = b.score;
        if (scoreA < scoreB) return 1;
        else if(scoreA > scoreB) return -1;
        else return 0;
      });
      return techCopy;
    },
    chooseBestTechnologies: function (techList) { //choose 6 most scored (or more in case of a draw)
      let _this = this;
      let mostScored = [];  //to store the final best technologies
      let minScore = 0;
      let done = false;
      let i = 0;
      while (! done && i < techList.length){
        if(i<6){
          mostScored.push(techList[i]);
          minScore = techList[i].score;
        }else{
          if(techList[i].score === minScore)
            mostScored.push(techList[i]);
          else done = true;
        }
        i++;
      }
      return mostScored;
    }
  },
  watch: {
    criteria_to_compute: {
      handler: function () {
        let _this = this;
        if(_this.technologies.length !== 0) {
          const techList = JSON.parse(JSON.stringify(_this.technologies))
          let sortedScoredTechs = _this.computeScore(techList);
          //_this.best_technologies = _this.chooseBestTechnologies(sortedScoredTechs);
          _this.best_technologies = sortedScoredTechs;
        }
      },
      deep: true,
    },
  },

}

</script>

