  color: rgba(255, 255, 255, 0.75);
}

hr.solid{
  border-top: 3px solid #bbb;
  margin-top: 0;
  margin-bottom: 0.5rem;
}
.navbar {
  /* background-color: rgb(1, 30, 65); */
  background-color: rgb(239, 51, 64)
}
.active {
  background-color: rgb(238, 116, 122);
}


