<template>
  <div>
    <div class="context accordion1" role="tablist">
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-button class="title blue-button" block v-b-toggle.context_criteria><b>CONTEXT CRITERIA</b></b-button>
        </b-card-header>
        <b-collapse id="context_criteria" visible accordion="my-accordion1" role="tabpanel">
          <b-card-body>
            <div class="row">
              <div class="mt-sm-0 mt-lg-0 col-sm-12 col-lg-6">
                <p class="text-left font-weight-bold mb-0" style="font-size: small">Is any residual protection needed?</p>
                <b-form-select
                    v-model="criteria.context_values.residual_protection"
                    :options="options.context.residual_protection"
                    required
                    size="sm"
                    class="mt-1">
                </b-form-select>
              </div>
              <div class="mt-sm-3 mt-lg-0 col-sm-12 col-lg-6">
                <p class="text-left font-weight-bold mb-0" style="font-size: small">Will it be implemented at household or institutional level?</p>
                <b-form-select
                    v-model="criteria.context_values.household_institution"
                    :options="options.context.household_institution"
                    required
                    size="sm"
                    class="mt-1"
                    v-on:change="updateContextCriteria('household_institution')"
                >
                </b-form-select>
                <b-row class="mt-2" v-if="(criteria.chosen_criteria.findIndex(x => x.name === 'household_institution') !== -1)">
                  <b-form-checkbox
                      v-on:change="changeWeightStatus('household_institution')"
                      class="mr-2 mt-1 ml-5"
                      checked="false"
                      size="sm"
                      switch
                  >
                    edit %
                  </b-form-checkbox>
                  <b-form-input
                      type="number"
                      style="width: 6rem"
                      :state="maxAvailableWeight('household_institution')"
                      size="sm"
                      v-model.number="criteria.chosen_criteria[criteria.chosen_criteria.findIndex(x => x.name === 'household_institution')].weight"
                      :disabled="isWeightFixed('household_institution')"
                      v-on:input="updateWeights"
                  >
                  </b-form-input>
                </b-row>
              </div>
            </div>
            <div class="row">
              <div class="mt-3 col-sm-12 col-lg-6">
                <p class="text-left font-weight-bold mb-0" style="font-size: small">Is the water turbid?</p>
                <b-form-select
                    v-model="criteria.context_values.treats_turbid_water"
                    :options="options.context.treats_turbid_water"
                    required
                    size="sm"
                    class="mt-1"
                    v-on:change="updateContextCriteria('treats_turbid_water')"
                >
                </b-form-select>
                <b-row class="mt-2" v-if="(criteria.chosen_criteria.findIndex(x => x.name === 'treats_turbid_water') !== -1)">
                  <b-form-checkbox
                      v-on:change="changeWeightStatus('treats_turbid_water')"
                      class="mr-2 mt-1 ml-5"
                      checked="false"
                      size="sm"
                      switch
                  >
                    edit %
                  </b-form-checkbox>
                  <b-form-input
                      type="number"
                      style="width: 6rem"
                      :state="maxAvailableWeight('treats_turbid_water')"
                      size="sm"
                      v-model.number="criteria.chosen_criteria[criteria.chosen_criteria.findIndex(x => x.name === 'treats_turbid_water')].weight"
                      :disabled="isWeightFixed('treats_turbid_water')"
                      v-on:input="updateWeights"
                  >
                  </b-form-input>
                </b-row>
              </div>
              <div class="mt-3 col-sm-12 col-lg-6">
                <p class="text-left font-weight-bold mb-0" style="font-size: small">Which kind of household water treatment technology was used previously in this community?</p>
                <b-form-select
                    v-model="criteria.context_values.method"
                    :options="options.context.method"
                    required
                    size="sm"
                    class="mt-1"
                    v-on:change="updateContextCriteria('method')"
                >
                </b-form-select>
                <b-row class="mt-2" v-if="(criteria.chosen_criteria.findIndex(x => x.name === 'method') !== -1)">
                  <b-form-checkbox
                      v-on:change="changeWeightStatus('method')"
                      class="mr-2 mt-1 ml-5"
                      checked="false"
                      size="sm"
                      switch
                  >
                    edit %
                  </b-form-checkbox>
                  <b-form-input
                      type="number"
                      style="width: 6rem"
                      :state="maxAvailableWeight('method')"
                      size="sm"
                      v-model.number="criteria.chosen_criteria[criteria.chosen_criteria.findIndex(x => x.name === 'method')].weight"
                      :disabled="isWeightFixed('method')"
                      v-on:input="updateWeights"
                  >
                  </b-form-input>
                </b-row>
              </div>
            </div>
          </b-card-body>
        </b-collapse>
      </b-card>
    </div>

    <div class="intrinsic accordion2" role="tablist">
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-button class="title blue-button" block v-b-toggle.intrinsic_criteria><b>INTRINSIC CRITERIA</b></b-button>
        </b-card-header>
        <b-collapse id="intrinsic_criteria" visible accordion="my-accordion2" role="tabpanel">
          <b-card-body>
            <p class="text-left font-weight-bold mb-0" style="font-size: small">Choose which criteria would you like to consider when scoring the technologies:</p>
            <b-form-group class="text-left">
              <b-form-checkbox
                  v-for="(c, idx) in options.intrinsic"
                  :key="idx"
                  switch
                  v-on:change="updateIntrinsicCriteria(c.value)"
                  size="sm"
                  class="filter"
                  checked="true"
              >{{ c.text }}
                <b-row class="ml-2 mb-1" v-if="criteria.chosen_criteria.findIndex(x => x.name === c.value) !== -1">
                  <b-form-checkbox
                      v-on:change="changeWeightStatus(c.value)"
                      class="mr-2 mt-1"
                      checked="false"
                  >
                    edit %
                  </b-form-checkbox>
                  <b-form-input
                      type="number"
                      style="width: 6rem"
                      :state="maxAvailableWeight(c.value)"
                      size="sm"
                      v-model.number="criteria.chosen_criteria[criteria.chosen_criteria.findIndex(x => x.name === c.value)].weight"
                      :disabled="isWeightFixed(c.value)"
                      v-on:input="updateWeights"
                  >
                  </b-form-input>
                </b-row>
              </b-form-checkbox>
            </b-form-group>
          </b-card-body>
        </b-collapse>
      </b-card>

    </div>
    <div class="mt-3">
      <b-button pill size="sm" class="m-2 title" style="width: 11rem; background-color: var(--blue-color)" v-on:click="$parent.$data.tab = 1"><b>Go to result</b></b-button>
    </div>

  </div>
</template>

<script>
  export default {
    name: "Criteria",
    data() {
      return {
        criteria: {
          context_values: {
            residual_protection: null,
            method: null,
            household_institution: null,
            treats_turbid_water: null
          },
          chosen_criteria: []
        },
        options: {
          context: {
            residual_protection: [
              {value: null, text: 'Please select an option'},
              {value: 'Yes', text: 'Yes'},
              {value: 'No', text: 'No'},
            ],
            method: [
              {value: null, text: 'Please select an option'},
              {value: 'Filtration', text: 'Filtration'},
              {value: 'UV disinfection', text: 'UV disinfection'},
              {value: 'Chemical disinfection', text: 'Chemical disinfection'},
              {value: 'Sedimentation', text: 'Sedimentation'},
              {value: 'None', text: 'None'},
            ],
            household_institution: [
              {value: null, text: 'Please select an option'},
              {value: 'HH', text: 'Household'},
              {value: 'Institution', text: 'Institution'},
              {value: 'No preference', text: 'No preference'},
            ],
            treats_turbid_water: [
              {value: null, text: 'Please select an option'},
              {value: 'Yes', text: 'Yes'},
              {value: 'No', text: 'No'},
            ]
          },
          intrinsic: [
            { text: 'WHO Performance', value: 'who_perf_target', fixedWeight: "true" },
            { text: 'Maintenance', value: 'maintenance', fixedWeight: "true" },
            { text: 'Instantaneous water supply', value: 'inst_water_supply', fixedWeight: "true" },
            { text: 'Power requirements', value: 'power_requirements', fixedWeight: "true" },
            { text: 'Easiness of understanding', value: 'easiness_use', fixedWeight: "true" },
            { text: 'Taste alteration', value: 'taste_alteration', fixedWeight: "true" },
            { text: 'Waste generation', value: 'waste_generation', fixedWeight: "true" }
          ]
        }
      }
    },
    mounted: function(){
      let criteria = ['who_perf_target', 'maintenance', 'inst_water_supply', 'power_requirements',
        'easiness_use', 'taste_alteration', 'waste_generation'];
      for(let i in criteria){
        let obj = {
          name: criteria[i],
          fixedWeight: true,
          weight: 14.28
        }
        this.criteria.chosen_criteria.push(obj);
      }
      this.updateWeights();
      this.$emit("childCriteria", this.criteria);
    },
    methods: {
      maxAvailableWeight: function(c){
        let _this = this;
        if(_this.criteria.chosen_criteria.length !== 0) {
          let cumWeight = 0.00;  //cumulative weight of the unfixed criteria .
          let cWeight = 0.00;
          for (let i in _this.criteria.chosen_criteria) {
            if (_this.criteria.chosen_criteria[i].name !== c) {
              cumWeight += _this.criteria.chosen_criteria[i].weight;
            }else {
              cWeight = _this.criteria.chosen_criteria[i].weight;
            }
          }
          if(!this.isWeightFixed(c)) {
            cumWeight = Math.round( ( cumWeight + Number.EPSILON ) * 100 ) / 100;
            return cWeight + cumWeight === 100.00 && cWeight <= 100.00 && cWeight >= 0.00;
          }
          else return cWeight <= 100 && cWeight >= 0;
        }
        else return true;
      },
      computeWeight: function(selectedCriteria){
        let weight = 100/selectedCriteria;
        weight = Math.round( ( weight + Number.EPSILON ) * 100 ) / 100;
        this.$emit("childCriteria", this.criteria);
        return weight;
      },
      changeWeightStatus: function(c){
        let _this = this;
        let idx = _this.criteria.chosen_criteria.findIndex(x => x.name === c);
        let status = _this.criteria.chosen_criteria[idx].fixedWeight;
        _this.criteria.chosen_criteria[idx].fixedWeight = !status;

        this.updateWeights(); //readjust weights for the fixed criteria to assure the overall sum of 100.
      },
      isWeightFixed: function(c){
        let _this = this;
        let idx = _this.criteria.chosen_criteria.findIndex(x => x.name === c);
        return _this.criteria.chosen_criteria[idx].fixedWeight;
      },
      updateWeights: function(){
        let _this = this;

        let nFixed = 0;               //number of chosen criteria with fixed weight
        let cumWeightUnfixed = 0.00;  //cumulative weight of the unfixed criteria .
        for(let i in _this.criteria.chosen_criteria){
          if(_this.criteria.chosen_criteria[i].fixedWeight === true) nFixed++;
          else cumWeightUnfixed += _this.criteria.chosen_criteria[i].weight;
        }

        let fixedWeight;
        let remain;
        if(cumWeightUnfixed > 100) {
          fixedWeight = 0;
          remain = 0;
        }
        else {
          //compute the "new" weight for the fixed criteria.
          fixedWeight = Math.round((((100.00 - cumWeightUnfixed) / nFixed) + Number.EPSILON) * 100) / 100; //round to 2 decimals

          //check the remain to assure the 100 overall sum, after rounding the weights for the fixed criteria.
          //the remain will be added to one of the fixed criteria (the first on the list)
          remain = Math.round( ( (100 - ((fixedWeight * nFixed) + cumWeightUnfixed)) + Number.EPSILON ) * 100 ) / 100;
        }

        let remainAdded = false;
        for(let i in _this.criteria.chosen_criteria){
          if(_this.criteria.chosen_criteria[i].fixedWeight === true) {
            if (remain !== 0 && remainAdded === false) { //it exists a "remain" after rounding. we add it to first fixed criteria.
              _this.criteria.chosen_criteria[i].weight = Math.round( ( fixedWeight + remain + Number.EPSILON ) * 100 ) / 100;
              remainAdded = true;
            }
            else
              _this.criteria.chosen_criteria[i].weight = fixedWeight;
          }
        }
      },
      updateIntrinsicCriteria: function (c) {
        let _this = this;
        let idx = _this.criteria.chosen_criteria.findIndex(x => x.name === c);
        if (idx === -1) {
          let obj = {
            name: c,
            fixedWeight: true,
            weight: this.computeWeight(_this.criteria.chosen_criteria.length + 1)
          }
          _this.criteria.chosen_criteria.push(obj);
        }else {
          _this.criteria.chosen_criteria.splice(idx, 1);
        }
        this.updateWeights(); //readjust weights for the fixed criteria to assure the overall sum of 100.
      },
      updateContextCriteria: function (c) {
        let _this = this;
        let idx = _this.criteria.chosen_criteria.findIndex(x => x.name === c);
        let addToList;
        let input = _this.criteria.context_values[c];

        if(c === 'method'){
          ((input === null) || (input === 'None')) ? addToList = false : addToList = true;
        }else if (c === 'treats_turbid_water'){
          (input === null) ? addToList = false :  addToList = true;
        }else if (c === 'household_institution'){
          ((input === null) || (input === 'No preference')) ? addToList = false :  addToList = true;
        }

        if (idx === -1) { //it is not in the list of chosen criteria and we have to add it.
          if(addToList === true){
            let obj = {
              name: c,
              fixedWeight: true,
              weight: this.computeWeight(_this.criteria.chosen_criteria.length + 1)
            }
            _this.criteria.chosen_criteria.push(obj);
          }
        }else
          if(addToList === false)
            _this.criteria.chosen_criteria.splice(idx, 1);

        this.updateWeights(); //readjust weights for the fixed criteria to assure the overall sum of 100.
      },
    },
  }
</script>

<style scoped>
hr.solid{
  border-top: 3px solid #bbb;
  margin-top: 0;
  margin-bottom: 0.5rem;
}
</style>
